* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
h1,
h2,
h3 {
  color: rgb(53, 52, 52);
}
body {
  font-family: "Lato", sans-serif;
}

h3,
h4 {
  font-weight: 400;
  color: rgb(100, 99, 99);
}
@import "./player";
@import "./song";
@import "./library";
@import "./nav";

.App {
  transition: all 0.5s ease;
}

.library-active {
  margin-left: 14%;
}
